import blog from '../../../src/api/blog'
import {mapMutations} from "vuex";


export default {
  name: 'HelloWorld',
  data () {
    return {
      title: '',
      description: '',
      content: '',
      atIndex: false,
      textarea2: '',
      textarea3: ''
    }
  },
  created(){
    this.onShow()
  },
  computed: {
    titleNumber(){
      return this.title.length
    },
    descriptionNumber(){
      return this.description.length
    },
    contentNumber(){
      return this.content.length
    }
  },
  methods: {
    ...mapMutations(['show','hide']),
    onShow(){
      this.show()
    },
    onHide(){
      this.hide()
    },
    onCreate(){
      console.log(this.title, this.description, this.content)
      blog.createBlog({
        title: this.title,
        description:this.description,
        content: this.content,
        atIndex: this.atIndex
      }).then(res=>{
        console.log(res)
        this.$message.success(res.msg)
        this.$router.push({ path: `/detail/${res.data.id}`})
      })
    }
  }
}



